import React, { useState, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App() {
    const [notes, setNotes] = useState([]);
    const [newNote, setNewNote] = useState('');
    const [usernameInput, setUsernameInput] = useState('');
    const [passwordInput, setPasswordInput] = useState('');
    const [authenticated, setAuthenticated] = useState(false);
    const [userInfo, setUserInfo] = useState({ username: '', roles: [] });

    // Загрузка заметок
    const fetchNotes = async () => {
        try {
            const response = await fetch('/api/notes', { credentials: 'include' });
            if (response.ok) {
                const data = await response.json();
                setNotes(data);
            } else {
                console.error('Ошибка загрузки заметок');
            }
        } catch (error) {
            console.error('Ошибка при загрузке заметок:', error);
        }
    };

    // Проверка авторизации
    const checkAuth = async () => {
        try {
            const response = await fetch('/api/auth/me', { credentials: 'include' });
            if (response.ok) {
                const user = await response.json();
                // Здесь важный момент: роли — это массив объектов { authority: "..." }
                const roles = user.roles.map(role => role.authority);
                setAuthenticated(true);
                setUserInfo({ username: user.username, roles: roles });
                fetchNotes();
            } else {
                setAuthenticated(false);
            }
        } catch (error) {
            console.error('Ошибка проверки авторизации:', error);
            setAuthenticated(false);
        }
    };

    useEffect(() => {
        checkAuth();
    }, []);

    // Логин
    const handleLogin = async (event) => {
        event.preventDefault();

        const formData = new URLSearchParams();
        formData.append('username', usernameInput);
        formData.append('password', passwordInput);

        try {
            const response = await fetch('/login', {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                body: formData,
                credentials: 'include'
            });

            if (response.ok) {
                await checkAuth();
                if (authenticated) {
                    toast.success(`Добро пожаловать, ${userInfo.username}!`);
                }
            } else {
                toast.error('Ошибка авторизации!');
                setAuthenticated(false);
            }
        } catch (error) {
            console.error('Ошибка логина:', error);
            toast.error('Ошибка соединения с сервером!');
            setAuthenticated(false);
        }
    };

    // Logout
    const handleLogout = async () => {
        try {
            const response = await fetch('/logout', {
                method: 'POST',
                credentials: 'include'
            });

            if (response.ok) {
                setAuthenticated(false);
                setUserInfo({ username: '', roles: [] });
                setNotes([]);
                toast.success('Вы успешно вышли из системы');
            } else {
                console.error('Ошибка при выходе.');
            }
        } catch (error) {
            console.error('Ошибка logout:', error);
        }
    };

    // Создание заметки
    const handleCreateNote = async (event) => {
        event.preventDefault();
        if (!newNote.trim()) return;

        try {
            const response = await fetch('/api/notes', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ content: newNote }),
                credentials: 'include'
            });

            if (response.ok) {
                setNewNote('');
                fetchNotes();
            } else {
                console.error('Ошибка создания заметки');
            }
        } catch (error) {
            console.error('Ошибка при создании заметки:', error);
        }
    };

    // Удаление заметки
    const handleDeleteNote = async (id) => {
        if (!window.confirm('Удалить заметку?')) return;

        try {
            const response = await fetch(`/api/notes/${id}`, {
                method: 'DELETE',
                credentials: 'include'
            });

            if (response.ok) {
                fetchNotes();
            } else {
                console.error('Ошибка удаления заметки');
            }
        } catch (error) {
            console.error('Ошибка при удалении заметки:', error);
        }
    };

    return (
    <div className="App">
    <h1>Сервис Заметок</h1>

    {authenticated ? (
    <>
    <div>
    <h3>Пользователь: {userInfo.username}</h3>
<h4>Роли: {userInfo.roles.join(', ')}</h4>
<button onClick={handleLogout}>Выйти</button>
</div>

<form onSubmit={handleCreateNote}>
<h2>Создать заметку</h2>
<input
type="text"
placeholder="Введите текст заметки"
value={newNote}
onChange={(e) => setNewNote(e.target.value)}
/>
<button type="submit">Создать</button>
</form>

<h2>Список заметок</h2>
<ul>
{notes.map((note) => (
<li key={note.id}>
<div dangerouslySetInnerHTML={{ __html: note.content }} />
<button onClick={() => handleDeleteNote(note.id)}>Удалить</button>
</li>
))}
</ul>
</>
) : (
<form onSubmit={handleLogin}>
<h2>Вход</h2>
<input
type="text"
placeholder="Имя пользователя"
value={usernameInput}
onChange={(e) => setUsernameInput(e.target.value)}
autoComplete="username"
/>
<input
type="password"
placeholder="Пароль"
value={passwordInput}
onChange={(e) => setPasswordInput(e.target.value)}
autoComplete="current-password"
/>
<button type="submit">Войти</button>
</form>
)}

<ToastContainer />
</div>
);
}

export default App;
